.header {
	height: rem(76);
	

	background-color: #FEFEFE;
	box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, .06);

	.container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
		height: 100%;
	}

	&__right {
		display: flex;
		flex-wrap: wrap;

		&-button {
			margin-right: rem(40);

			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.item {
				position: relative;
				margin-right: rem(24);

				span {
					position: absolute;
					top: rem(-4);
					right: rem(-6);
					font-size: rem(8);
					color: white;
					background-color: var(--c-main);
					width: rem(12);
					height: rem(12);
					border-radius: 50%;
					display: flex;
					align-items: center;
					justify-content: center;
				}

				&:last-child {
					margin-right: 0;
				}

				&:hover {
					i::before {
						color: var(--c-main);
					}
				}
			}

			i {
				font-size: rem(24);

				&::before {
					transition: all 200ms ease;
				}
			}
		}

		&-user {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			.first {
				margin-right: rem(28);

				&:hover {
					color: var(--c-main);
				}
			}

			.user_panel {
				margin-right: rem(24);
			}
		}
	}
}

.user_panel {
	position: relative;
	
	&__drop {
		position: absolute;
		top: rem(60);
		left: rem(-24);
		width: rem(242);
		border-radius: rem(10);
		padding: rem(20) 0;
		background-color: white;
		box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);
		z-index: 100;
		display: none;

		.count {
			padding: 0 rem(20) rem(10) rem(20);
			margin-bottom: rem(20);
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			border-bottom: rem(1) solid #B2ABCD;

			span {
				&:first-child {
					margin-right: rem(7);
				}
			}
			
			&__list {
				margin-right: rem(8);
				display: flex;
				flex-wrap: wrap;
				align-items: center;

				i {
					font-size: rem(12);

					margin-right: rem(3);

					&:last-child {
						margin-right: 0;
					}
				}
			}
		}

		ul {
			padding: 0 rem(20);

			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}
				a {

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}
	}

	&__drop.active_drop {
		display: block;
	}
	

	&__button {
		cursor: pointer;
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&:hover {
			span,
			.icon-arrow-bottom::before {
				color: var(--c-main);
			}
		}

		.icon-profile-user {
			pointer-events: none;
			font-size: rem(26);
			margin-right: rem(6);
		}
		span {
			pointer-events: none;
			margin-right: rem(7);
		}
		.icon-arrow-bottom {
			pointer-events: none;
			font-size: rem(8);
		}
	}

	&__button.active {
		span,
		.icon-arrow-bottom::before {
			color: var(--c-main);
		}
	}
}