*,
*::after,
*::before {
  box-sizing: border-box;
}

blockquote,
body,
dd,
dl,
figcaption,
figure,
h1,
h2,
h3,
h4,
li,
ol,
p,
ul {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}

ol,
ul {
  padding: 0;
  list-style: none;
}

a {
  font-family: inherit;
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

button {
  cursor: pointer;
  border: none;
}

button,
input,
select,
textarea {
  font-family: inherit;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Bold"), local("Roboto Bold"), url("../fonts/Roboto/Roboto-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Regular"), local("Roboto Regular"), url("../fonts/Roboto/Roboto-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Light"), local("Roboto Light"), url("../fonts/Roboto/Roboto-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Roboto';
  src: local("Roboto Medium"), local("Roboto Medium"), url("../fonts/Roboto/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

:root {
  --f-default: 'Roboto';
  --c-main: #E8511D;
  --c-text: #1B1B1B;
  --c-text-gray: #797C88;
  --c-main-bg: #F8FAFD;
}

* {
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
}

html, body {
  position: relative;
}

html {
  font-size: 100%;
}

button {
  background-color: transparent;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

h1, .main-title {
  font-size: 2rem;
  font-weight: bold;
}

body {
  color: var(--c-text);
  font-family: var(--f-default);
  font-size: 1rem;
  background-color: var(--c-main-bg);
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.relative {
  position: relative;
}

.w-8\/12 {
  flex: 0 0 66.666667%;
  max-width: 66.666667%;
}

.w-7\/12 {
  flex: 0 0 58.333333%;
  max-width: 58.333333%;
}

.w-6\/12 {
  flex: 0 0 50%;
  max-width: 50%;
}

.w-5\/12 {
  flex: 0 0 41.666667%;
  max-width: 41.666667%;
}

.w-4\/12 {
  flex: 0 0 33.333333%;
  max-width: 33.333333%;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.block {
  display: block;
}

.flex {
  display: flex;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-col {
  flex-direction: column;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.flex-between {
  justify-content: space-between;
}

.items-center {
  align-items: center !important;
}

.items-start {
  align-items: flex-start !important;
}

.items-end {
  align-items: flex-end !important;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-left {
  text-align: left;
}

.page_main {
  margin-top: 1.875rem;
}

.button_orange {
  padding: 0 1.75rem;
  height: 2.5rem;
  background-color: var(--c-main);
  color: white;
  border-radius: 4.25rem;
  font-weight: bold;
  font-size: 0.875rem;
  border: 0.0625rem solid var(--c-main);
  display: flex;
  align-items: center;
  transition: all 200ms ease;
}

.button_orange:hover {
  background-color: white;
  color: var(--c-main);
}

.button_orange_r {
  padding: 0 1.75rem;
  height: 2.5rem;
  background-color: transparent;
  border-radius: 4.25rem;
  font-weight: bold;
  font-size: 0.875rem;
  border: 0.125rem solid var(--c-main);
  display: flex;
  align-items: center;
  transition: all 200ms ease;
}

.button_orange_r:hover {
  background-color: var(--c-main);
  color: white;
}

.button_border {
  padding: 0 1.75rem;
  height: 2.5rem;
  background-color: white;
  color: var(--c-text-gray);
  border-radius: 4.25rem;
  font-weight: bold;
  font-size: 0.875rem;
  border: 0.0625rem solid var(--c-text-gray);
  display: flex;
  align-items: center;
  transition: all 200ms ease;
}

.button_border:hover {
  background-color: var(--c-main);
  border-color: var(--c-main);
  color: white;
}

.field-set {
  position: relative;
}

.field-set label {
  margin-bottom: 0.9375rem;
  font-weight: bold;
}

.field-set .message-error {
  position: absolute;
  bottom: -1.25rem;
  left: 0;
  font-size: 0.75rem;
  color: var(--c-main);
}

.field {
  font-size: 1rem;
  color: var(--c-text-gray);
  padding: 0 1.625rem;
  height: 2.5rem;
  border-radius: 0.375rem;
  border: 0.0625rem solid #B6B6B6;
  transition: all 200ms ease;
}

.field:hover, .field:focus {
  border-color: var(--c-main);
}

.field::-webkit-input-placeholder {
  color: var(--c-text-gray);
}

.field:-moz-placeholder {
  color: var(--c-text-gray);
}

.field::-moz-placeholder {
  color: var(--c-text-gray);
}

.field:-ms-input-placeholder {
  color: var(--c-text-gray);
}

.field-error {
  border-color: var(--c-main);
}

.header {
  height: 4.75rem;
  background-color: #FEFEFE;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.06);
}

.header .container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.header__right {
  display: flex;
  flex-wrap: wrap;
}

.header__right-button {
  margin-right: 2.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__right-button .item {
  position: relative;
  margin-right: 1.5rem;
}

.header__right-button .item span {
  position: absolute;
  top: -0.25rem;
  right: -0.375rem;
  font-size: 0.5rem;
  color: white;
  background-color: var(--c-main);
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header__right-button .item:last-child {
  margin-right: 0;
}

.header__right-button .item:hover i::before {
  color: var(--c-main);
}

.header__right-button i {
  font-size: 1.5rem;
}

.header__right-button i::before {
  transition: all 200ms ease;
}

.header__right-user {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.header__right-user .first {
  margin-right: 1.75rem;
}

.header__right-user .first:hover {
  color: var(--c-main);
}

.header__right-user .user_panel {
  margin-right: 1.5rem;
}

.user_panel {
  position: relative;
}

.user_panel__drop {
  position: absolute;
  top: 3.75rem;
  left: -1.5rem;
  width: 15.125rem;
  border-radius: 0.625rem;
  padding: 1.25rem 0;
  background-color: white;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
  z-index: 100;
  display: none;
}

.user_panel__drop .count {
  padding: 0 1.25rem 0.625rem 1.25rem;
  margin-bottom: 1.25rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  border-bottom: 0.0625rem solid #B2ABCD;
}

.user_panel__drop .count span:first-child {
  margin-right: 0.4375rem;
}

.user_panel__drop .count__list {
  margin-right: 0.5rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.user_panel__drop .count__list i {
  font-size: 0.75rem;
  margin-right: 0.1875rem;
}

.user_panel__drop .count__list i:last-child {
  margin-right: 0;
}

.user_panel__drop ul {
  padding: 0 1.25rem;
}

.user_panel__drop ul li {
  margin-bottom: 0.625rem;
}

.user_panel__drop ul li:last-child {
  margin-bottom: 0;
}

.user_panel__drop ul li a:hover {
  color: var(--c-main);
}

.user_panel__drop.active_drop {
  display: block;
}

.user_panel__button {
  cursor: pointer;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.user_panel__button:hover span,
.user_panel__button:hover .icon-arrow-bottom::before {
  color: var(--c-main);
}

.user_panel__button .icon-profile-user {
  pointer-events: none;
  font-size: 1.625rem;
  margin-right: 0.375rem;
}

.user_panel__button span {
  pointer-events: none;
  margin-right: 0.4375rem;
}

.user_panel__button .icon-arrow-bottom {
  pointer-events: none;
  font-size: 0.5rem;
}

.user_panel__button.active span,
.user_panel__button.active .icon-arrow-bottom::before {
  color: var(--c-main);
}

.filter_form {
  display: flex;
  flex-direction: column;
  margin-bottom: 2.875rem;
}

.filter_form__fields {
  display: flex;
  margin-bottom: 1rem;
}

.filter_form__fields .custom-select {
  max-width: 11.25rem;
  flex: 0 0 11.25rem;
  background-color: var(--c-main-bg);
}

.filter_form__fields .select-selected {
  background-color: var(--c-main-bg);
}

.filter_form__fields .first .select-selected {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-right: 0;
}

.filter_form__fields .last .select-selected {
  border-radius: 0;
}

.filter_form__fields .filter-input .field {
  border-left: 0;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background-color: var(--c-main-bg);
}

.filter_form__fields .filter-input {
  position: relative;
  flex-grow: 1;
}

.filter_form__fields .filter-input .field {
  width: 98%;
  padding-right: 6.25rem;
  border-color: #9A9EBC;
}

.filter_form__fields .filter-input .button_orange {
  position: absolute;
  top: 0;
  right: 0;
}

.filter_form__checked {
  display: flex;
  flex-wrap: wrap;
}

.filter_form__checked .field-checked {
  cursor: pointer;
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
}

.filter_form__checked .field-checked:last-child {
  margin-right: 0;
}

.filter_form__checked .field-checked input {
  display: none;
}

.filter_form__checked .field-checked input:checked ~ .check::before {
  left: auto;
  right: 0;
  background-color: var(--c-main);
}

.filter_form__checked .field-checked .check {
  position: relative;
  margin-right: 0.3125rem;
  width: 1.75rem;
  height: 0.5625rem;
  border-radius: 3.75rem;
  background-color: #E3E2E3;
}

.filter_form__checked .field-checked .check::before {
  transition: all 200ms ease;
  content: "";
  position: absolute;
  top: -0.25rem;
  left: 0;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: #CDC1C1;
  box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, 0.01);
}

.main_page__header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 1.875rem;
}

.main_page__header h1 {
  margin-right: 1.125rem;
}

.main_page__header span {
  color: var(--c-text-gray);
  font-size: 2rem;
}

.main_page__header .back {
  width: 100%;
  margin-top: 1rem;
}

.main_page__header .back__link {
  display: flex;
  align-items: center;
}

.main_page__header .back__link:hover {
  color: var(--c-main);
}

.main_page__header .back i {
  margin-right: 0.5625rem;
  font-size: 1.375rem;
}

.main_page__wrap {
  display: flex;
}

.main_page__aside {
  flex: 0 0 12.5rem;
  margin-right: 2.5rem;
  border-right: 0.0625rem solid #C6C8DE;
}

.main_page__aside .title {
  margin-bottom: 0.9375rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.main_page__aside .title b {
  font-size: 1.125rem;
  margin-right: 0.4375rem;
}

.main_page__aside .title i {
  font-size: 0.75rem;
}

.main_page__aside .list {
  margin-left: 0.625rem;
}

.main_page__aside .list li {
  line-height: 200%;
}

.main_page__aside .list li a {
  transition: all 200ms ease;
  color: var(--c-text-gray);
}

.main_page__aside .list li a:hover {
  color: var(--c-main);
}

.main_page__content {
  flex: 0 0 60rem;
}

.main_page__items {
  display: flex;
  flex-wrap: wrap;
}

.main_page__items .item {
  width: calc((100% - 3.75rem) / 4);
  margin-right: 1.25rem;
  margin-bottom: 1.25rem;
  border-radius: 0.625rem;
  overflow: hidden;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
}

.main_page__items .item:nth-child(4n) {
  margin-right: 0;
}

.main_page__items .item__image {
  position: relative;
  height: 9.375rem;
  margin-bottom: 0.8125rem;
}

.main_page__items .item__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main_page__items .item__image-folow {
  position: absolute;
  z-index: 5;
  top: 0.625rem;
  right: 0.625rem;
  width: 0.875rem;
  height: 0.875rem;
  border-radius: 50%;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main_page__items .item__image-folow:hover i::before {
  color: var(--c-main);
}

.main_page__items .item__image-folow i {
  font-size: 0.5rem;
  margin-top: 0.0625rem;
}

.main_page__items .item__image-folow i::before {
  color: #A1A0A0;
}

.main_page__items .item__text {
  padding: 0 0.9375rem 1.375rem 0.9375rem;
}

.main_page__items .item__text .title {
  font-size: 1rem;
  margin-bottom: 0.625rem;
}

.main_page__items .item__text p {
  color: var(--c-text-gray);
  margin-bottom: 0.625rem;
}

.main_page__items .item__text .price {
  font-size: 1.125rem;
  color: var(--c-main);
}

.main_page__map {
  border-radius: 0.625rem;
  overflow: hidden;
}

.main_page__map .map_control {
  height: 40rem;
}

.main_page__map .map_control .marker:first-child {
  left: 16rem;
  top: 28.25rem;
}

.main_page__map .map_control .marker:nth-child(3) {
  left: 31.125rem;
  top: 25.375rem;
}

.main_page__map .map_control .marker:nth-child(4) {
  top: 8.875rem;
  left: 36.875rem;
}

.main_page__map .map_control .marker:nth-child(5) {
  left: 38.125rem;
  top: 33.875rem;
}

.main_page__map .map_control .marker:nth-child(6) {
  left: 44.5rem;
  top: 19.5625rem;
}

.main_page__map .map_control .marker:nth-child(7) {
  top: 18.3125rem;
  right: 11.75rem;
}

.main_page__map .map_control .marker:nth-child(8) {
  top: 25.375rem;
  right: 5.625rem;
}

.main_page__map .map_control .locale {
  position: absolute;
  top: 13.125rem;
  left: 29.9375rem;
}

.main_page__map .map_control .locale:hover .locale__item {
  display: flex;
}

.main_page__map .map_control .locale i {
  position: relative;
  z-index: 11;
  font-size: 1.75rem;
}

.map_control {
  overflow: hidden;
  border-radius: 0.625rem;
  margin-bottom: 2.5rem;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
}

.map_control__iframe {
  position: relative;
  height: 13.9375rem;
}

.map_control__iframe .locale__item {
  right: 0.625rem;
  bottom: 0.625rem;
  position: absolute;
  width: 23.125rem;
  background-color: white;
  border-radius: 0.625rem;
  padding: 1.5rem;
  flex-wrap: wrap;
  z-index: 10;
  box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.08);
  display: none;
}

.map_control__iframe .locale__item-folow {
  position: absolute;
  top: 1.6875rem;
  right: 0.9375rem;
  z-index: 5;
}

.map_control__iframe .locale__item-folow:hover i::before {
  color: var(--c-main);
}

.map_control__iframe .locale__item-folow i {
  font-size: 0.8125rem !important;
}

.map_control__iframe .locale__item-image {
  margin-right: 0.875rem;
  width: 6.875rem;
  height: 5.75rem;
  border-radius: 0.625rem;
  overflow: hidden;
}

.map_control__iframe .locale__item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.map_control__iframe .locale__item-text {
  flex: 0 0 11.125rem;
  max-width: 11.125rem;
}

.map_control__iframe .locale__item-text .title {
  font-size: 1rem;
  margin-bottom: 0.25rem;
}

.map_control__iframe .locale__item-text p {
  margin-bottom: 0.5rem;
  color: var(--c-text-gray);
}

.map_control__iframe .locale__item-text .price {
  color: var(--c-main);
  font-size: 1.125rem;
  font-weight: bold;
}

.map_control__iframe .marker {
  position: absolute;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: var(--c-main);
}

.map_control__iframe .marker:first-child {
  top: 4.5rem;
  left: 9.125rem;
}

.map_control__iframe .marker:nth-child(2) {
  bottom: 1.625rem;
  left: 17.8125rem;
}

.map_control__iframe .marker:nth-child(3) {
  top: 5.4375rem;
  left: 26.1875rem;
}

.map_control__iframe .marker:nth-child(4) {
  left: 36.3125rem;
  bottom: 2.875rem;
}

.map_control__iframe .marker:nth-child(5) {
  top: 5.4375rem;
  right: 14.3125rem;
}

.map_control__button {
  height: 3rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 3.75rem;
}

.breadcrumbs a {
  position: relative;
  color: var(--c-text-gray);
  margin-right: 2.125rem;
}

.breadcrumbs a:hover {
  color: var(--c-main);
}

.breadcrumbs a::before {
  pointer-events: none;
  content: "/";
  position: absolute;
  top: 0;
  right: -1.0625rem;
  color: var(--c-text-gray);
}

.article {
  display: flex;
  flex-wrap: wrap;
}

.article .w-7\/12 {
  padding-right: 2.1875rem;
}

.article__slider {
  margin-bottom: 1.875rem;
}

.article__slider hr {
  margin-top: 1.875rem;
  width: 100%;
  height: 0.0625rem;
  background-color: #B2ABCD;
}

.article__title {
  margin-bottom: 0.625rem;
}

.article__subtitle {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 1.25rem;
}

.article__subtitle > span {
  font-size: 0.75rem;
  margin-right: 1.125rem;
}

.article__subtitle-view {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}

.article__subtitle-view i {
  font-size: 1rem;
  margin-right: 0.25rem;
}

.article__subtitle-view span {
  font-size: 0.75rem;
}

.article__price {
  margin-bottom: 1.25rem;
}

.article__price b {
  font-size: 2rem;
}

.article__favorite {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 1.5rem;
}

.article__favorite i, .article__favorite span {
  color: var(--c-text-gray);
}

.article__favorite i {
  font-size: 1.5rem;
  margin-right: 0.5rem;
}

.article__favorite span {
  text-decoration: underline;
}

.article__favorite:hover i::before,
.article__favorite:hover span {
  color: var(--c-main);
}

.article__favorite:hover span {
  text-decoration: none;
}

.article__button {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.625rem;
}

.article__button a, .article__button button {
  width: 17.6875rem;
  height: 3.125rem;
  font-size: 1.125rem;
  display: flex;
  justify-content: center;
}

.article__button a:first-child, .article__button button:first-child {
  margin-bottom: 1.25rem;
}

.article .company {
  width: 17rem;
}

.article__images {
  height: 27rem;
  margin-bottom: 1.25rem;
  border-radius: 0.625rem;
  overflow: hidden;
}

.article__images .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.article__images_thumb .swiper-slide {
  cursor: pointer;
  position: relative;
  height: 5rem;
  border-radius: 0.625rem;
  overflow: hidden;
}

.article__images_thumb .swiper-slide:hover::before {
  opacity: 1;
}

.article__images_thumb .swiper-slide::before {
  content: "";
  position: absolute;
  border-radius: 0.625rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: inset 0 0 0 3px var(--c-main);
  z-index: 3;
  transition: all 200ms ease;
  opacity: 0;
}

.article__images_thumb .swiper-slide img {
  border-radius: 0.625rem;
  position: relative;
  z-index: 2;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.article__images_thumb .swiper-slide-thumb-active::before {
  content: "";
  position: absolute;
  border-radius: 0.625rem;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: inset 0 0 0 3px var(--c-main);
  z-index: 3;
  transition: all 200ms ease;
  opacity: 1;
}

