.main_page {

	&__header {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(30);

		h1 {
			margin-right: rem(18);
		}

		span {
			color: var(--c-text-gray);
			font-size: rem(32);
		}

		.back {
			width: 100%;
			margin-top: rem(16);

			&__link {
				display: flex;
				align-items: center;

				&:hover {
					color: var(--c-main);
				}
			}

			i {
				margin-right: rem(9);
				font-size: rem(22);
			}
		}
	}

	&__wrap {
		display: flex;
		// flex-wrap: wrap;
	}

	&__aside {
		flex: 0 0 rem(200);
		margin-right: rem(40);
		border-right: rem(1) solid #C6C8DE;

		.title {
			margin-bottom: rem(15);
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			b {
				font-size: rem(18);
				margin-right: rem(7);
			}

			i {
				font-size: rem(12);
			}
		}

		.list {
			margin-left: rem(10);
			li {
				line-height: 200%;

				a {
					transition: all 200ms ease;
					color: var(--c-text-gray);

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}
	}

	&__content {
		flex: 0 0 rem(960);
		// flex: 0 0 auto;
	}

	&__items {
		display: flex;
		flex-wrap: wrap;

		.item {
			width: calc((100% - 3.75rem) / 4);
			margin-right: rem(20);
			margin-bottom: rem(20);

			border-radius: rem(10);
			overflow: hidden;

			box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

			&:nth-child(4n) {
				margin-right: 0;
			}

			&__image {
				position: relative;
				height: rem(150);
				margin-bottom: rem(13);

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}

				&-folow {
					position: absolute;
					z-index: 5;
					top: rem(10);
					right: rem(10);
					width: rem(14);
					height: rem(14);
					border-radius: 50%;
					background-color: white;

					display: flex;
					align-items: center;
					justify-content: center;

					&:hover {
						i::before {
							color: var(--c-main);
						}
					}

					i {
						font-size: rem(8);
						margin-top: rem(1);

						&::before {
							color: #A1A0A0;
						}
					}
				}
			}

			&__text {
				padding: 0 rem(15) rem(22) rem(15);

				.title {
					font-size: rem(16);
					margin-bottom: rem(10);
				}

				p {
					color: var(--c-text-gray);
					margin-bottom: rem(10);
				}

				.price {
					font-size: rem(18);
					color: var(--c-main);
				}
			}
		}
	}

	&__map {
		border-radius: rem(10);
		overflow: hidden;

		.map_control {
			height: rem(640);

			.marker {
				&:first-child {
					left: rem(256);
					top: rem(452);
				}
				&:nth-child(3) {
					left: rem(498);
					top: rem(406);
				}
				&:nth-child(4) {
					top: rem(142);
					left: rem(590);
				}
				&:nth-child(5) {
					left: rem(610);
					top: rem(542);
				}
				&:nth-child(6) {
					left: rem(712);
					top: rem(313);
				}
				&:nth-child(7) {
					top: rem(293);
					right: rem(188);
				}
				&:nth-child(8) {
					top: rem(406);
					right: rem(90);
				}
			}

			.locale {
				position: absolute;
				top: rem(210);
				left: rem(479);

				&:hover {
					.locale__item {
						display: flex;
					}
				}
				i {
					position: relative;
					z-index: 11;
					font-size: rem(28);
				}
			}
		}
	}
}

.map_control {
	overflow: hidden;
	border-radius: rem(10);
	margin-bottom: rem(40);

	box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);


	&__iframe {
		position: relative;

		height: rem(223);

		.locale {
			
			&__item {
				right: rem(10);
				bottom: rem(10);
				position: absolute;
				width: rem(370);
				background-color: white;
				border-radius: rem(10);
				padding: rem(24);

				// display: flex;
				flex-wrap: wrap;
				z-index: 10;
				box-shadow: 0px 4px 5px 0px rgba(0, 0, 0, .08);

				display: none;

				&-folow {
					position: absolute;
					top: rem(27);
					right: rem(15);
					z-index: 5;

					&:hover {
						i::before {
							color: var(--c-main);
						}
					}

					i {
						font-size: rem(13) !important;
					}
				}

				&-image {
					margin-right: rem(14);

					width: rem(110);
					height: rem(92);
					border-radius: rem(10);
					overflow: hidden;

					img {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}

				&-text {
					flex: 0 0 rem(178);
					max-width: rem(178);

					.title {
						font-size: rem(16);
						margin-bottom: rem(4);
					}

					p {
						margin-bottom: rem(8);
						color: var(--c-text-gray);
					}

					.price {
						color: var(--c-main);
						font-size: rem(18);
						font-weight: bold;
					}
				}
			}
		}

		.marker {
			position: absolute;
			width: rem(20);
			height: rem(20);
			border-radius: 50%;
			background-color: var(--c-main);

			&:first-child {
				top: rem(72);
				left: rem(146);
			}
			&:nth-child(2) {
				bottom: rem(26);
				left: rem(285);
			}
			&:nth-child(3) {
				top: rem(87);
				left: rem(419);
			}
			&:nth-child(4) {
				left: rem(581);
				bottom: rem(46);
			}
			&:nth-child(5) {
				top: rem(87);
				right: rem(229);
			}
		}
	}

	&__button {
		height: rem(48);
		display: flex;
		align-items: center;
		justify-content: center;

	}
}