.breadcrumbs {
	display: flex;
	flex-wrap: wrap;

	margin-bottom: rem(60);

	a {
		position: relative;
		color: var(--c-text-gray);
		margin-right: rem(34);

		&:hover {
			color: var(--c-main);
		}

		&::before {
			pointer-events: none;
			content: "/";
			position: absolute;
			top: 0;
			right: rem(-17);
			color: var(--c-text-gray);
		}
	}
	
}

.article {

	display: flex;
	flex-wrap: wrap;

	.w-7\/12 {
		padding-right: rem(35);
	}

	&__slider {
		margin-bottom: rem(30);
		hr {
			margin-top: rem(30);
			width: 100%;
			height: rem(1);
			background-color: #B2ABCD;
		}
	}

	&__title {
		margin-bottom: rem(10);
	}

	&__subtitle {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		margin-bottom: rem(20);

		> span {
			font-size: rem(12);
			margin-right: rem(18);
		}

		&-view {
			display: flex;
			flex-wrap: wrap;
			align-items: center;

			i {
				font-size: rem(16);
				margin-right: rem(4);
			}

			span {
				font-size: rem(12);
			}
		}
	}

	&__price {
		margin-bottom: rem(20);

		b {
			font-size: rem(32);
		}
	}

	&__favorite {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		margin-bottom: rem(24);

		i,span {
			color: var(--c-text-gray);
		}

		i {
			font-size: rem(24);
			margin-right: rem(8);
		}

		span {
			text-decoration: underline;
			
		}

		&:hover {

			i::before,
			span {
				color: var(--c-main);
			}

			span {
				text-decoration: none;
			}
		}
	}

	&__button {
		display: flex;
		flex-direction: column;
		margin-bottom: rem(26);

		a,button {
			width: rem(283);
			height: rem(50);
			font-size: rem(18);
			display: flex;
			justify-content: center;

			&:first-child {
				margin-bottom: rem(20);
			}
		}
	}

	.company {
		width: rem(272);
	}

	&__images {

		height: rem(432);
		margin-bottom: rem(20);
		border-radius: rem(10);
		overflow: hidden;

		.swiper-slide {
			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&__images_thumb {
		.swiper-slide {
			cursor: pointer;
			position: relative;
			height: rem(80);
			border-radius: rem(10);
			overflow: hidden;

			&:hover {
				&::before {
					opacity: 1;
				}
			}

			&::before {
				content: "";
				position: absolute;
				border-radius: rem(10);
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				box-shadow: inset 0 0 0 3px var(--c-main);
				z-index: 3;
				transition: all 200ms ease;
				opacity: 0;
			}

			img {
				border-radius: rem(10);
				position: relative;
				z-index: 2;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.swiper-slide-thumb-active {
			&::before {
				content: "";
				position: absolute;
				border-radius: rem(10);
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				box-shadow: inset 0 0 0 3px var(--c-main);
				z-index: 3;
				transition: all 200ms ease;
				opacity: 1;
			}
		}
	}

	&__text {
		.locale {
			margin-bottom: rem(6);
		}

		.circl {
			position: relative;
			display: block;
			padding-left: rem(26);

			&::before {
				content: "";
				position: absolute;
				top: rem(4);
				left: 0;
				width: rem(11);
				height: rem(11);
				background-color: var(--c-main);
				border-radius: 50%;
			}
		}

		&-map {
			position: relative;
			margin-top: rem(20);
			margin-bottom: rem(45);
			border-radius: rem(10);
			overflow: hidden;

			height: rem(223);

			.locale {
				position: absolute;
				top: rem(97);
				left: rem(315);

				i {
					font-size: rem(28);
				}
			}
		}

		ul {
			margin-top: rem(20);

			li {
				margin-bottom: rem(10);

				&:last-child {
					margin-bottom: 0;
				}
				span {
					&:first-child {
						color: var(--c-text-gray);
						margin-right: rem(4);
					}
				}
			}
		}
	}
}


.company {
	border: rem(1) solid #B6B6B6;
	border-radius: rem(10);
	padding: rem(30);

	&__title {
		margin-bottom: rem(5);
	}

	&__stats {
		margin-bottom: rem(8);

		display: flex;
		flex-wrap: wrap;
		align-items: center;

		span {
			font-size: rem(12);

			&:first-of-type {
				margin-right: rem(6);
			}
		}

		&-list {
			display: flex;
			flex-wrap: wrap;
			margin-right: rem(6);

			i {
				font-size: rem(10);
				margin-right: rem(2);

				&:last-child {
					margin-right: 0;
				}
			}
		}
	}

	&__contact {
		margin-bottom: rem(10);

		span {
			&:first-child {
				color: var(--c-text-gray);
				margin-right: rem(4);
			}
		}
	}

	&__button {

		.button_border {
			display: flex;
			justify-content: center;
			height: rem(34);

			font-weight: normal;
			color: var(--c-text);

			&:hover {
				color: white;
			}
		}
	}
}