footer.footer {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	background-color: #FEFEFE;
	margin-top: rem(75);

	padding: rem(16) 0;

	p {
		font-size: rem(12);
		color: var(--c-text-gray);
		margin-right: rem(64);
	}

	a {
		font-size: rem(12);
		color: var(--c-text-gray);

		&:hover {
			text-decoration: underline;
		}
	}
}