.filter_form {
	display: flex;
	flex-direction: column;
	margin-bottom: rem(46);

	&__fields {
		display: flex;
		margin-bottom: rem(16);
		
		.custom-select {
			max-width: rem(180);
			flex: 0 0 rem(180);
			background-color: var(--c-main-bg);
		}

		.select-selected {
			background-color: var(--c-main-bg);
		}

		.first {
			.select-selected {
				border-top-right-radius: 0;
				border-bottom-right-radius: 0;
				border-right: 0;
				
			}
		}

		.last {
			.select-selected {
				border-radius: 0;
				// border-right: 0;
			}
		}

		.filter-input {
			.field {
				border-left: 0;
				border-top-left-radius: 0;
				border-bottom-left-radius: 0;
				background-color: var(--c-main-bg);
			}
		}

		.filter-input {
			position: relative;
			flex-grow: 1;

			.field {
				width: 98%;
				padding-right: rem(100);
				border-color: #9A9EBC;
			}

			.button_orange {
				position: absolute;
				top: 0;
				right: 0;
			}
		}
	}

	&__checked {
		display: flex;
		flex-wrap: wrap;

		.field-checked {
			cursor: pointer;
			display: flex;
			align-items: center;

			margin-right: rem(12);

			&:last-child {
				margin-right: 0;
			}

			input {
				display: none;

				&:checked ~ .check {
					&::before {
						left: auto;
						right: 0;
						background-color: var(--c-main);
					}
				}
			}

			.check {
				position: relative;
				margin-right: rem(5);
				width: rem(28);
				height: rem(9);
				border-radius: rem(60);
				background-color: #E3E2E3;

				&::before {
					transition: all 200ms ease;
					content: "";
					position: absolute;
					top: rem(-4);
					left: 0;
					width: rem(16);
					height: rem(16);
					border-radius: 50%;
					background-color: #CDC1C1;
					box-shadow: 0px 2px 5px 0px rgba(0, 0, 0, .01);
				}
			}
		}
	}
}