:root {
	--f-default: 'Roboto';
	--c-main: #E8511D;
	--c-text: #1B1B1B;
	--c-text-gray: #797C88;

	--c-main-bg: #F8FAFD;
}

* {
	margin: 0;
	padding: 0;
	outline: none;
	border: 0;
}

html, body {
	position: relative;
	// overflow-x: hidden;
	// height: 100%;
	// overscroll-behavior: none;
}

html {
	font-size: 100%;
	// height: 100%;	
}

button {
	background-color: transparent;
}

a {
	text-decoration: none;
}

li {
	list-style: none;
}

h1, .main-title {
	font-size: rem(32);
	font-weight: bold;
}

body {
	// position: relative;
	color: var(--c-text);
	font-family: var(--f-default);
	font-size: rem(16);
	background-color: var(--c-main-bg);
	// height: 100%;
	// overflow-x: hidden;
	// -webkit-overflow-scrolling: touch;
}

.container {
	max-width: 1200px;
	margin: 0 auto;
}

.relative {
	position: relative;
}

.w-8\/12 {
	flex: 0 0 66.666667%;
	max-width: 66.666667%
}

.w-7\/12 {
	flex: 0 0 58.333333%;
	max-width: 58.333333%;
}

.w-6\/12 {
	flex: 0 0 50%;
	max-width: 50%;
}

.w-5\/12 {
	flex: 0 0 41.666667%;
	max-width: 41.666667%;
}

.w-4\/12 {
	flex: 0 0 33.333333%;
	max-width: 33.333333%
}

.w-full {
	width: 100%;
}
.h-full {
	height: 100%;
}

.block {
	display: block;
}
.flex {
	display: flex;
}
.flex-wrap {
	flex-wrap: wrap;
}
.flex-col {
	flex-direction: column;
}
.flex-end {
	justify-content: flex-end;
}
.flex-center {
	justify-content: center;
}
.flex-between {
	justify-content: space-between;
}
.items-center {
	align-items: center !important;
}
.items-start {
	align-items: flex-start !important;
}
.items-end {
	align-items: flex-end !important;
}



.text-center {
	text-align: center;
}
.text-right {
	text-align: right;
}
.text-left {
	text-align: left;
}

.page_main {
	margin-top: rem(30);
}

.button_orange {
	padding: 0 rem(28);
	height: rem(40);
	background-color: var(--c-main);
	color: white;
	border-radius: rem(68);
	font-weight: bold;
	// text-transform: uppercase;
	font-size: rem(14);
	border: rem(1) solid var(--c-main);

	display: flex;
	align-items: center;

	transition: all 200ms ease;

	&:hover {
		background-color: white;
		color: var(--c-main);
	}
}

.button_orange_r {
	padding: 0 rem(28);
	height: rem(40);
	background-color: transparent;
	// color: white;
	border-radius: rem(68);
	font-weight: bold;
	// text-transform: uppercase;
	font-size: rem(14);
	border: rem(2) solid var(--c-main);

	display: flex;
	align-items: center;

	transition: all 200ms ease;

	&:hover {
		background-color: var(--c-main);
		color: white;
	}
}

.button_border {
	padding: 0 rem(28);
	height: rem(40);
	background-color: white;
	color: var(--c-text-gray);
	border-radius: rem(68);
	font-weight: bold;
	// text-transform: uppercase;
	font-size: rem(14);
	border: rem(1) solid var(--c-text-gray);

	display: flex;
	align-items: center;

	transition: all 200ms ease;

	&:hover {
		background-color: var(--c-main);
		border-color: var(--c-main);
		color: white;
	}
}

.field-set {
	position: relative;
	label {
		margin-bottom: rem(15);
		font-weight: bold;
	}

	.message-error {
		position: absolute;
		bottom: rem(-20);
		left: 0;
		font-size: rem(12);
		color: var(--c-main);
	}
}

.field {
	font-size: rem(16);
	color: var(--c-text-gray);
	padding: 0 rem(26);
	height: rem(40);
	border-radius: rem(6);
	border: rem(1) solid #B6B6B6;

	transition: all 200ms ease;

	&:hover,&:focus {
		border-color: var(--c-main);
	}
	

	@include placeholder {
		color: var(--c-text-gray);
	}
}

.field-error {
	border-color: var(--c-main);
}